import React, { useState } from "react"
import clsx from "clsx"

import TextField from "@material-ui/core/TextField"
import { makeStyles } from "@material-ui/core/styles"
import Toolbar from "@material-ui/core/Toolbar"
import Typography from "@material-ui/core/Typography"

import estilos from "./estilos.module.css"

const useToolbarStyles = makeStyles((theme) => ({
	root: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(1),
		height: 68,
	},
	spacer: {
		flex: "1 1 5%",
	},
	actions: {
		color: theme.palette.text.secondary,
		display: "inline-block",
	},
	title: {
		flex: "0 0 auto",
	},
}))

const EnhancedTableToolbar = (props) => {
	const classes = useToolbarStyles()
	const [inputValue, setInputValue] = useState("")
	const {
		searchedRowsCallback,
		fetchedRows,
		colunasTabela,
		tituloTabela,
	} = props

	const onSearchInputChangedHandler = (event) => {
		setInputValue(event.target.value)
		const query = event.target.value
		let isSearching = false
		if (query.length > 0) {
			isSearching = true
		} else {
			isSearching = false
		}

		let searchedRows = []
		fetchedRows.map((row) => {
			let like = false
			colunasTabela.map((col) => {
				if (
					String(row[col.id]).toUpperCase().search(query.toUpperCase()) !== -1
				)
					like = true
			})
			if (like) searchedRows.push(row)
			return row
		})

		searchedRowsCallback(searchedRows, isSearching)
	}

	return (
		<Toolbar className={[clsx(classes.root), estilos.actions].join(" ")}>
			<div className={classes.title}>
				<Typography variant="h6" id="tableTitle" className={estilos.TableTitle}>
					{tituloTabela}
				</Typography>
			</div>
			<div className={[classes.spacer, estilos.spacer].join(" ")} />
			<div className={[classes.actions, estilos.actions].join(" ")}>
				{!!props.caixaPesquisa ? (
					<TextField
						key={"pesquisar"}
						label="Pesquisa"
						className={estilos.SearchInput}
						placeholder="..."
						margin="dense"
						variant="filled"
						value={inputValue}
						onChange={(event) => onSearchInputChangedHandler(event)}
					/>
				) : null}
			</div>
		</Toolbar>
	)
}

export default EnhancedTableToolbar
