import axios from "../../config/axios"
import authServerIp from "../../config/auth-server-ip"
import { getUserToken } from "../functions/auth"

export const fetchSessoesUsuariosHardt = async () => {
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post(authServerIp + "/sessoes-hardt", {}, request)

    if (!res.data.usuarios) {
      return []
    }

    return res.data.usuarios
  } catch (err) {
    throw err
  }
}

export const finalizarSessaoUsuarioHardt = async (dados) => {
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post(
      authServerIp + "/finalizar-sessao-usuario-hardt",
      { id_sessao: dados.id_sessao },
      request
    )

    return res.data.success
  } catch (err) {
    throw err
  }
}

export const fetchSessoesUsuariosSafig = async () => {
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post(authServerIp + "/sessoes-safig", {}, request)

    if (!res.data) {
      return []
    }

    return res.data
  } catch (err) {
    throw err
  }
}

export const finalizarSessaoUsuarioSafig = async (dados) => {
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post(
      authServerIp + "/finalizar-sessao-usuario-safig",
      {
        id_cliente: dados.id_cliente,
        id_sessao: dados.id_sessao,
      },
      request
    )

    return res.data.success
  } catch (err) {
    throw err
  }
}
