import React, { useState, useEffect } from "react"

import * as clienteAPI from "../../../services/api/cliente"
import * as usuarioAPI from "../../../services/api/usuario"
import * as sessoesAPI from "../../../services/api/sessoes-usuarios"
import ErrorBoundary from "../../../components/ErrorBoundary"
import Datatable from "../../../components/Datatable"
import Table from "../../../components/Table"
import Tooltip from "../../../components/UI/Tooltip/Tooltip"
import IconButton from "@material-ui/core/IconButton"
import { DesktopAccessDisabled } from "@material-ui/icons"
import estilos from "./estilos.module.css"

const Usuarios = (props) => {
  const { appState } = props

  const [sessoes, setSessoes] = useState([])
  const [clientesComSessoes, setClientesComSessoes] = useState([])
  const [rows, setRows] = useState([])
  const [clientes, setClientes] = useState([])
  const [usuarios, setUsuarios] = useState([])
  const [linhaSelecionadaClientes, setLinhaSelecionadaClientes] = useState(null)

  const colunasTabelaClientes = [
    {
      id: "codigo",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Código",
    },
    {
      id: "nome",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Nome",
    },
    {
      id: "email_login",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Email",
    },
  ]

  const colunasTabelaUsuarios = [
    {
      id: "id_usuario",
      type: "data",
      label: "Nome",
    },
    {
      id: "autoridade",
      type: "data",
      label: "Autoridade",
    },
    {
      id: "ip",
      type: "data",
      label: "IP",
    },
    {
      id: "expiration_date",
      type: "data",
      label: "Validade sessão",
    },
    {
      id: "actions",
      type: "actions",
      label: "Ações",
    },
  ]

  const idToNameClientes = () => {
    if (!clientes || !sessoes) {
      return
    }

    if (clientes.length === 0) {
      return
    }

    let newClientesList = []

    for (const id_cliente in sessoes) {
      const cli = clientes.find((c) => String(c.id) === String(id_cliente))
      if (cli) {
        newClientesList.push(cli)
      }
    }

    setClientesComSessoes(newClientesList)
  }

  const idToNameUsuarios = () => {
    if (!linhaSelecionadaClientes) {
      return
    }

    if (!sessoes[linhaSelecionadaClientes]) {
      setRows([])
      return
    }

    let newS = sessoes[linhaSelecionadaClientes].map((s) => {
      const usuario = usuarios.find((u) => u.id === s.id_usuario)

      return {
        ...s,
        id_usuario: usuario.usr_login,
        expiration_date: new Date(s.expiration_date).toLocaleString(),
        actions: [
          <Tooltip
            enterDelay={0}
            enterTouchDelay={0}
            title={"Finalizar sessão"}
          >
            <IconButton onClick={() => endSession(s.id_sessao)}>
              <DesktopAccessDisabled />
            </IconButton>
          </Tooltip>,
        ],
      }
    })

    setRows(newS)
  }

  const successHandler = () => {
    fetchSessoesUsuariosSafigHandler()
    appState.setSuccess()
  }

  const clearRowsHandler = async () => {
    setRows([])
  }

  const fetchSessoesUsuariosSafigHandler = async () => {
    const res = await sessoesAPI.fetchSessoesUsuariosSafig({ appState })

    if (res) {
      setSessoes(res)
    }
  }

  const fetchClientesHandler = async () => {
    const res = await clienteAPI.fetchAll({ appState })

    if (res) {
      setClientes(res)
    }
  }

  const fetchUsuariosHandler = async () => {
    const res = await usuarioAPI.fetchAll({
      appState,
      id_cliente: linhaSelecionadaClientes,
    })

    if (res) {
      setUsuarios(res)
    }
  }

  const endSession = async (id_sessao) => {
    const res = await sessoesAPI.finalizarSessaoUsuarioSafig({
      appState,
      id_cliente: linhaSelecionadaClientes,
      id_sessao,
    })

    if (res) {
      successHandler()
    }
  }

  useEffect(() => {
    fetchSessoesUsuariosSafigHandler().then(() => {
      fetchClientesHandler()
    })
  }, [])

  useEffect(() => {
    idToNameClientes()
  }, [clientes, sessoes])

  useEffect(() => {
    if (linhaSelecionadaClientes !== null) {
      fetchUsuariosHandler()
    } else {
      clearRowsHandler()
    }
  }, [linhaSelecionadaClientes])

  useEffect(() => {
    idToNameUsuarios()
  }, [usuarios, clientesComSessoes])

  return (
    <ErrorBoundary>
      <div className={estilos.Container}>
        <div className={estilos.Tabelas}>
          <Datatable
            caixaPesquisa={true}
            colunasKey={"id"}
            colunasTabela={colunasTabelaClientes}
            fetchedRows={clientesComSessoes}
            linhasPorPaginaSelecionada={5}
            linhaSelecionada={linhaSelecionadaClientes}
            loading={appState.loading}
            opcoesTabela={[]}
            opcoesTabelaSelecionada={[]}
            setLinhaSelecionada={setLinhaSelecionadaClientes}
            tituloTabela={"Clientes com sessões ativas"}
          />

          <Table
            caixaPesquisa={true}
            colunasKey={"id_sessao"}
            colunasTabela={colunasTabelaUsuarios}
            fetchedRows={rows}
            loading={appState.loading}
            tituloTabela={"Sessões"}
          />
        </div>
      </div>
    </ErrorBoundary>
  )
}

export default Usuarios
