import React, { useState, useEffect } from "react"

import Table from "@material-ui/core/Table"
import TableBody from "@material-ui/core/TableBody"
import TableCell from "@material-ui/core/TableCell"
import TableRow from "@material-ui/core/TableRow"
import Paper from "@material-ui/core/Paper"

import TableToolbar from "./TableToolbar"
import { EnhancedTableHead, stableSort, getSorting } from "./TableHead"
import estilos from "./estilos.module.css"

const CustomTable = (props) => {
	const [rows, setRows] = useState([])
	const [isSearching, setIsSearching] = useState(false)
	const [searchedRows, setSearchedRows] = useState([])

	const {
		colunasKey,
		colunasTabela,
		fetchedRows,
		loading,
		tituloTabela,
	} = props

	const onChangeSearchedRows = (rows, isSearchingCb) => {
		setSearchedRows(rows)
		setIsSearching(isSearchingCb)
	}

	useEffect(() => {
		setRows(fetchedRows ? fetchedRows : [])
	}, [fetchedRows])

	const [order, setOrder] = useState("asc")
	const [orderBy, setOrderBy] = useState(colunasKey)

	const handleRequestSort = (event, property) => {
		const isDesc = orderBy === property && order === "desc"
		setOrder(isDesc ? "asc" : "desc")
		setOrderBy(property)
	}

	const genCellContent = (row, col, key) => {
		if (col.type === "data") {
			return (
				<div key={key}>
					{String(row[col.id]).slice(0, 60)}
					{String(row[col.id]).length > 60 ? "..." : null}
				</div>
			)
		} else if (col.type === "actions") {
			return row.actions.map((ac) => {
				return <div key={key}>{ac}</div>
			})
		}

		return <div key={key}>{""}</div>
	}

	return (
		<div className={estilos.Root}>
			<Paper className={estilos.Paper}>
				<TableToolbar
					caixaPesquisa={props.caixaPesquisa}
					colunasTabela={colunasTabela}
					fetchedRows={fetchedRows}
					loading={loading}
					rows={rows}
					searchedRowsCallback={onChangeSearchedRows}
					tituloTabela={tituloTabela}
				/>
				<div className={estilos.TableWrapper}>
					<Table size={"small"}>
						<EnhancedTableHead
							order={order}
							orderBy={orderBy}
							onRequestSort={handleRequestSort}
							rowCount={isSearching ? searchedRows.length : rows.length}
							headRows={colunasTabela}
						/>

						<TableBody>
							{stableSort(
								isSearching ? searchedRows : rows,
								getSorting(order, orderBy)
							).map((row, index) => (
								<TableRow
									hover
									key={row[colunasKey] + "" + index}
									tabIndex={-1}
								>
									{colunasTabela.map((col, i) => (
										<TableCell
											key={row[colunasKey] + "" + index + "" + i}
											align={col.align}
										>
											{genCellContent(row, col, row[colunasKey])}
										</TableCell>
									))}
								</TableRow>
							))}
						</TableBody>
					</Table>
				</div>
			</Paper>
		</div>
	)
}

export default CustomTable
