import React, { useEffect, useState } from "react"
import { Route, useHistory } from "react-router-dom"

import SessoesHardt from "./SessoesHardt"
import SessoesSafig from "./SessoesSafig"
import estilos from "./estilos.module.css"

const Sessoes = (props) => {
  const history = useHistory()
  const location = history.location.pathname
  const [sess, setSess] = useState("hardt")

  useEffect(() => {
    setSess(String(history.location.pathname).split("/")[2])
  }, [location])

  return (
    <div className={estilos.Container}>
      <div className={estilos.Buttons}>
        <div
          className={[
            estilos.Button,
            sess === "hardt" ? estilos.Selected : null,
          ].join(" ")}
          onClick={() => {
            history.push("/sessoes/hardt")
            setSess("hardt")
          }}
        >
          Sessões Hardt
        </div>
        <div
          className={[
            estilos.Button,
            sess === "safig" ? estilos.Selected : "",
          ].join(" ")}
          onClick={() => {
            history.push("/sessoes/safig")
            setSess("safig")
          }}
        >
          Sessões Safig
        </div>
      </div>

      <div>
        <Route path="/sessoes/hardt">
          <SessoesHardt {...props} />
        </Route>
        <Route path="/sessoes/safig">
          <SessoesSafig {...props} />
        </Route>
      </div>
    </div>
  )
}

export default Sessoes
