import axios from "../../config/axios"
import { getUserToken } from "../functions/auth"
import isGraphQLError from "../functions/isGraphQLError"

export const fetchAll = async () => {
  const graphqlQuery = `
			{
				administradores {
					id
					nome
					status
					usuario
				}
			}
		`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.administradores
  } catch (err) {
    throw err
  }
}

export const fetchOne = async (dados) => {
  const graphqlQuery = `
		{
			administrador(id: "${dados.id}") {
				id
				usuario
				nome
				status
			}
		}
		`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.administrador
  } catch (err) {
    throw err
  }
}

export const create = async (dados) => {
  try {
    const graphqlQuery = `
			mutation {
				cadAdministrador(
					data: {
						usuario: "${dados.administrador.usuario}"
						senha: "${dados.administrador.senha}"
						nome: "${dados.administrador.nome}"
						status: ${dados.administrador.status}
					}
				)
			}
			`
    const request = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
        "Content-Type": "application/json",
      },
    }

    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return true
  } catch (err) {
    throw err
  }
}

