import React, { useState } from "react"
import clsx from "clsx"

import TextField from "@material-ui/core/TextField"
import { lighten, makeStyles } from "@material-ui/core/styles"
import Toolbar from "@material-ui/core/Toolbar"
import Typography from "@material-ui/core/Typography"
import IconButton from "@material-ui/core/IconButton"
import Tooltip from "../../UI/Tooltip/Tooltip"
import Menu from "../../UI/Menu"

import estilos from "./estilos.module.css"

const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
    height: 68,
  },
  highlight: {
    color: theme.palette.primary.main,
    backgroundColor: lighten(theme.palette.primary.light, 0.85),
  },
  spacer: {
    flex: "1 1 5%",
  },
  actions: {
    color: theme.palette.text.secondary,
    display: "inline-block",
  },
  title: {
    flex: "0 0 auto",
  },
}))

const EnhancedTableToolbar = (props) => {
  const classes = useToolbarStyles()
  const [inputValue, setInputValue] = useState("")
  const {
    searchedRowsCallback,
    fetchedRows,
    colunasTabela,
    selectedRow,
    loading,
    tituloTabela,
    opcoesTabelaSelecionada,
    opcoesTabela,
  } = props

  const onSearchInputChangedHandler = (event) => {
    setInputValue(event.target.value)
    const query = event.target.value
    let isSearching = false
    if (query.length > 0) {
      isSearching = true
    } else {
      isSearching = false
    }

    let searchedRows = []
    fetchedRows.map((row) => {
      let like = false
      colunasTabela.map((col) => {
        if (
          String(row[col.id]).toUpperCase().search(query.toUpperCase()) !== -1
        )
          like = true
      })
      if (like) searchedRows.push(row)
      return row
    })

    searchedRowsCallback(searchedRows, isSearching)
  }

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]:
          selectedRow !== null && opcoesTabelaSelecionada.length > 0,
      })}
    >
      <div className={classes.title}>
        {selectedRow !== null && opcoesTabelaSelecionada.length > 0 ? (
          <Typography
            variant="h6"
            id="tableTitle"
            className={estilos.TableTitle}
          >
            Ações
          </Typography>
        ) : (
          <Typography
            variant="h6"
            id="tableTitle"
            className={estilos.TableTitle}
          >
            {tituloTabela}
          </Typography>
        )}
      </div>
      <div className={[classes.spacer, estilos.spacer].join(" ")} />
      <div className={[classes.actions, estilos.actions].join(" ")}>
        {selectedRow !== null && opcoesTabelaSelecionada.length > 0 ? (
          <div className={estilos.TableActions}>
            {opcoesTabelaSelecionada.map((op, index) => (
              <Tooltip
                enterDelay={0}
                enterTouchDelay={0}
                key={op.label + index}
                title={op.label}
              >
                <IconButton
                  aria-label={op.label}
                  disabled={loading}
                  onClick={() => op.click()}
                >
                  {op.icon}
                </IconButton>
              </Tooltip>
            ))}
          </div>
        ) : (
          <React.Fragment>
            <span className={estilos.tableActionsMobile}>
              <Menu options={opcoesTabela} disabled={loading} />
            </span>
            <span className={estilos.tableActions}>
              {opcoesTabela.map((op, index) => (
                <Tooltip
                  className={estilos.toolbarBtn}
                  enterDelay={0}
                  enterTouchDelay={0}
                  key={op.label + index}
                  title={op.label}
                >
                  <IconButton
                    aria-label={op.label}
                    disabled={loading}
                    onClick={() => op.click()}
                  >
                    {op.icon}
                  </IconButton>
                </Tooltip>
              ))}
            </span>

            {!!props.caixaPesquisa ? (
              <TextField
                key={"pesquisar"}
                label="Pesquisa"
                className={estilos.SearchInput}
                placeholder="..."
                margin="dense"
                variant="filled"
                value={inputValue}
                onChange={(event) => onSearchInputChangedHandler(event)}
              />
            ) : null}
          </React.Fragment>
        )}
      </div>
    </Toolbar>
  )
}

export default EnhancedTableToolbar
