import React, { useState, useEffect } from "react"

import * as sessoesAPI from "../../../services/api/sessoes-usuarios"
import * as administradorAPI from "../../../services/api/administrador"
import idToName from "../../../functions/idToName"
import Table from "../../../components/Table"
import IconButton from "@material-ui/core/IconButton"
import { DesktopAccessDisabled } from "@material-ui/icons"
import Tooltip from "../../../components/UI/Tooltip/Tooltip"
import ErrorBoundary from "../../../components/ErrorBoundary"
import estilos from "./estilos.module.css"

const Sessoes = (props) => {
  const { appState } = props

  const [sessoes, setSessoes] = useState([])
  const [sessoesA, setSessoesA] = useState([])
  const [administradores, setAdministradores] = useState([])

  const colunasTabela = [
    {
      id: "usuario",
      type: "data",
      label: "Usuário",
    },
    {
      id: "nome",
      type: "data",
      label: "Nome",
    },
    {
      id: "ip",
      type: "data",
      label: "IP",
    },
    {
      id: "expirationDate",
      type: "data",
      label: "Validade sessão",
    },
    {
      id: "actions",
      type: "actions",
      label: "Ações",
    },
  ]

  const successHandler = () => {
    fetchSessoesHandler()
    appState.setSuccess()
  }

  const formatDataSessoes = () => {
    if (!sessoes) {
      return
    }
    if (!administradores) {
      return
    }

    let sess = []

    sess = sessoes.map((s) => {
      const admin = administradores.find((a) => {
        if (s.id_admin === a.id) {
          return true
        }
      })

      return {
        ...s,
        ...admin,
      }
    })

    if (administradores.length === 0) {
      return
    }

    sess = idToName([sess, "id_admin"], [administradores, "nome"])

    sess = sess.map((s) => {
      return {
        ...s,
        expirationDate: new Date(s.expirationDate).toLocaleString(),
        actions: [
          <Tooltip
            enterDelay={0}
            enterTouchDelay={0}
            title={"Finalizar sessão"}
          >
            <IconButton onClick={() => endSession(s.id_sessao)}>
              <DesktopAccessDisabled />
            </IconButton>
          </Tooltip>,
        ],
      }
    })

    setSessoesA(sess)
  }

  const fetchSessoesHandler = async () => {
    const res = await sessoesAPI.fetchSessoesUsuariosHardt({ appState })

    setSessoes(res)
  }

  const fetchAdministradoresHandler = async () => {
    const res = await administradorAPI.fetchAll({ appState })

    setAdministradores(res)
  }

  const endSession = async (id_sessao) => {
    const res = await sessoesAPI.finalizarSessaoUsuarioHardt({
      appState,
      id_sessao,
    })

    if (res) {
      successHandler()
    }
  }

  useEffect(() => {
    fetchSessoesHandler()
    fetchAdministradoresHandler()
  }, [])

  useEffect(() => {
    formatDataSessoes()
  }, [sessoes, administradores])

  return (
    <ErrorBoundary>
      <div className={estilos.Container}>
        <Table
          caixaPesquisa={true}
          colunasKey={"id_sessao"}
          colunasTabela={colunasTabela}
          fetchedRows={sessoesA}
          loading={appState.loading}
          tituloTabela={"Sessões"}
        />
      </div>
    </ErrorBoundary>
  )
}

export default Sessoes
