import React, { useState, useEffect } from "react"

import Table from "@material-ui/core/Table"
import TableBody from "@material-ui/core/TableBody"
import TableCell from "@material-ui/core/TableCell"
import TablePagination from "@material-ui/core/TablePagination"
import TableRow from "@material-ui/core/TableRow"
import Paper from "@material-ui/core/Paper"

import TableToolbar from "./TableToolbar"
import { EnhancedTableHead, stableSort, getSorting } from "./TableHead"
import estilos from "./estilos.module.css"

const DataTable = (props) => {
	const [rows, setRows] = useState([])
	const [isSearching, setIsSearching] = useState(false)
	const [searchedRows, setSearchedRows] = useState([])

	const {
		colunasKey,
		colunasTabela,
		deleteButton,
		fetchedRows,
		linhaSelecionada,
		loading,
		opcoesTabela,
		opcoesTabelaSelecionada,
		setLinhaSelecionada,
		tituloTabela,
	} = props

	const onChangeSearchedRows = (rows, isSearchingCb) => {
		setSearchedRows(rows)
		setIsSearching(isSearchingCb)
	}

	useEffect(() => {
		setRows(fetchedRows ? fetchedRows : [])
	}, [fetchedRows])

	const [order, setOrder] = useState("asc")
	const [orderBy, setOrderBy] = useState(colunasKey)
	const [page, setPage] = useState(0)
	const [rowsPerPage, setRowsPerPage] = useState(
		props.linhasPorPaginaSelecionada || 10
	)

	const handleRequestSort = (event, property) => {
		const isDesc = orderBy === property && order === "desc"
		setOrder(isDesc ? "asc" : "desc")
		setOrderBy(property)
	}

	const handleClick = (event, id) =>
		linhaSelecionada === id
			? setLinhaSelecionada(null)
			: setLinhaSelecionada(id)
	const handleChangePage = (event, newPage) => setPage(newPage)
	const handleChangeRowsPerPage = (event) => {
		setRowsPerPage(+event.target.value)
		setPage(0)
	}

	const isSelected = (id) => linhaSelecionada === id

	const emptyRows =
		rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage)

	return (
		<div className={estilos.Root}>
			<Paper className={estilos.Paper}>
				<TableToolbar
					caixaPesquisa={props.caixaPesquisa}
					colunasTabela={colunasTabela}
					deleteButton={deleteButton}
					fetchedRows={fetchedRows}
					loading={loading}
					opcoesTabela={opcoesTabela}
					opcoesTabelaSelecionada={opcoesTabelaSelecionada}
					rows={rows}
					searchedRowsCallback={onChangeSearchedRows}
					selectedRow={linhaSelecionada}
					tituloTabela={tituloTabela}
				/>
				<div className={estilos.TableWrapper}>
					<Table size={"small"}>
						<EnhancedTableHead
							order={order}
							orderBy={orderBy}
							onRequestSort={handleRequestSort}
							rowCount={isSearching ? searchedRows.length : rows.length}
							headRows={colunasTabela}
						/>

						<TableBody>
							{stableSort(
								isSearching ? searchedRows : rows,
								getSorting(order, orderBy)
							)
								.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
								.map((row, index) => (
									<TableRow
										className={isSelected(row["id"]) ? estilos.Selected : ""}
										hover
										onClick={(event) => handleClick(event, row["id"])}
										key={row["id"]}
										selected={isSelected(row["id"])}
										tabIndex={-1}
									>
										{colunasTabela.map((col, index) => (
											<TableCell key={row["id"] + "" + index} align={col.align}>
												{String(row[col.id] ? row[col.id] : "").slice(0, 60)}
												{String(row[col.id]).length > 60 ? "..." : null}
											</TableCell>
										))}
									</TableRow>
								))}
							{emptyRows > 0 && (
								<TableRow style={{ height: 49 * emptyRows }}>
									<TableCell colSpan={6} />
								</TableRow>
							)}
						</TableBody>
					</Table>
				</div>
				<TablePagination
					rowsPerPageOptions={[5, 7, 10, 15, 25, 50]}
					component="div"
					count={isSearching ? searchedRows.length : rows.length}
					rowsPerPage={rowsPerPage}
					labelRowsPerPage={<span className={estilos.LinhasPP}>Linhas</span>}
					page={page}
					backIconButtonProps={{
						"aria-label": "Página anterior",
					}}
					nextIconButtonProps={{
						"aria-label": "Próxima página",
					}}
					onChangePage={handleChangePage}
					onChangeRowsPerPage={handleChangeRowsPerPage}
				/>
			</Paper>
		</div>
	)
}

export default DataTable
